export const AcsessApi=async(languages,lang,text)=>{
    const url='https://script.google.com/macros/s/AKfycbwujfdMM4o2QBkzUdVoykT1HVPonpxbfd39mg-5cxZv7Ych0uH6qkLIIg/exec?'
    const params={}
    for(let i=0; i<languages.length;i++){
        params[`lang${i}`]=languages[i].code
    }
    params['text']=text
    params['source']=lang
    //params['crossDomain']=true
    const query=new URLSearchParams(params);
    const res = await fetch(url+query)
    const data=await res.json()
    return data
}